<style>
body {font-family: Helvetica, sans-serif;}
.iconClassRoot {
   width:15px;
   height:15px;
   display: inline-block;
   background: url("./images/ztree/root.png") no-repeat center/100% auto;
}
.iconClassNode {
   width:15px;
   height:15px;
   display: inline-block;
   background: url("./images/ztree/node.png") no-repeat center/100% auto;
}
</style>
<template>
  <div style='display:flex;flex:3'>
      <div style='flex:1'>
        <h1>Hello Ztree()</h1>
        <div style='width:280px;' v-if='ztreeDataSource.length>0'>
           <vue-ztree :list.sync='ztreeDataSource' :is-open='false'></vue-ztree>
        </div>
      </div>

      <div style='flex:1'>
        <h1>Hello Ztree()</h1>
        <div style='width:280px;' v-if='ztreeDataSourceSync.length>0'>
           <vue-ztree :list.sync='ztreeDataSourceSync' :func='nodeClick' :expand='expandClick'  :is-open='false'></vue-ztree>
        </div>
      </div>

      <div style='flex:1'>
        <h1>Hello Ztree()</h1>
        <div style='width:280px;' v-if='ztreeDataSourceList.length>0'>
           <vue-ztree :list.sync='ztreeDataSourceList' :contextmenu='contextmenuClick' :is-open='true'></vue-ztree>
        </div>
      </div>
  </div>
</template>

<script>
import Vue from 'vue'
import vueZtree from './component/vue-ztree.vue'
export default {
  data () {
    return {
      msg: 'Hello Vue-Ztree!',
      ztreeDataSource:[],
      ztreeDataSourceList:[{
          id:880,
          name:"",
          iconClass:"iconClassRoot",
          open:true,
          children:[{
            id:881,
            name:"",
            clickNode:true,
            iconClass:"iconClassNode",
          },{
            id:882,
            name:"",
            iconClass:"iconClassNode",
          },{
            id:883,
            name:"",
            iconClass:"iconClassNode",
          }]
      },{
          id:990,
          name:"BAT",
          iconClass:"iconClassRoot",
          open:false,
          children:[{
              id:991,
              name:"",
              iconClass:"iconClassNode",
          },{
              id:992,
              name:"",
              iconClass:"iconClassNode",
          },{
              id:993,
              name:"",
              iconClass:"iconClassNode",
          }]
      }],
      ztreeDataSourceSync:[{
          id:220,
          name:"",
          children:[{
            id:881,
            name:""
          }]
      }]
    }
  },
  methods:{
    // 
    nodeClick:function(m){
       console.log(JSON.parse(JSON.stringify(m)));
    },
    // 
    contextmenuClick:function(){
       console.log("contextmenuClick");
    },
    // 
    expandClick:function(m){
       console.log(JSON.parse(JSON.stringify(m)));
       // 
       if(m.isExpand) {
          // , ajax
         //  id 
         if(m.hasOwnProperty("children")){
            
            m.loadNode = 1; // 

            setTimeout(()=>{

              m.loadNode = 2; // 

              m.isFolder = !m.isFolder; 

              var node = JSON.parse(JSON.stringify(m));

             // , ajax
             //  id 
             if(node.hasOwnProperty("children")){
                  node.children.push({
                      id:+new Date(),
                      name:"1",
                      path:"",
                      clickNode:false,
                      isFolder:false,
                      isExpand:false,
                      loadNode:0,
                      children:[{
                            id:+new Date()+1,
                            name:"",
                            path:"",
                            clickNode:false,
                            isFolder:false,
                            isExpand:false,
                            loadNode:0
                      }]
                  });
                  
                  m.children = node.children;
             }

            },1000)
            
         }
       }
    }
  },
  components:{
    vueZtree
  },
  ready(){
    // 
      setTimeout(()=>{ 
         this.ztreeDataSource = [{
            id:220,
            name:"1",
            children:[{
              id:221,
              name:"2",
              path:"",
              children:[{
                  id:222,
                  name:"3",
                  path:"",
                  children:[{
                      id:223,
                      name:"4",
                      path:"",
                      children:[{
                          id:224,
                          name:"5",
                          path:"",
                          children:[{
                              id:225,
                              name:"6",
                              path:"",
                              children:[{
                                  id:226,
                                  name:"",
                                  path:""
                              }],
                          }],
                      }],
                  }],
              }],
           }],
            path:"http://www.baidu.com"
         },{
            id:1,
            name:"",
            children:[],
            path:"http://www.baidu.com"
         },{
            id:2,
            name:"",
            children:[{
               id:3,
               name:"",
               children:[{
                  id:4,
                  name:"",
                  path:""
               },{
                  id:5,
                  name:"",
                  path:""
               },{
                  id:6,
                  name:"",
                  path:""
               }]
            },{
               id:7,
               name:"",
               children:[{
                  id:9,
                  name:"",
                  path:""
               },{
                  id:10,
                  name:"",
                  path:""
               }]
            }],
            path:""
         }]
      },1000);
  }
}
</script>


